export class ProjectDetail {
  public screenShot: { srcImg: string, title: string, detail: string, ref: string }[] = [];
  public links :{title:string,link:string}[] = [];
  public details :any[]= [];
  public projectName = "";
  constructor(
    projectName: string,
    details = [],
    links: { title: string, link: string }[] = [],
    screenShot: { srcImg: string, title: string, detail: string, ref: string }[]=[],
  ) {
    this.projectName = projectName;
    this.details = details;
    this.links = links;
    this.screenShot = screenShot;
  }
}
