import { Component, OnInit } from '@angular/core';
import { ProjectDetail } from '../class/project-detail';
import { UnityProjectsData } from '../class/unity-projects-data';


@Component({
  selector: 'app-individual-projects',
  templateUrl: './individual-projects.page.html',
  styleUrls: ['./individual-projects.page.scss'],
})
export class IndividualProjectsPage implements OnInit {

  constructor() { }
  
  tabsMenuData: {
    tabsName: string[], projectsContent: any[] 
  }[] = [];
  
  ngOnInit() {
    UnityProjectsData.getDataList()
    this.initData();
  }
  initData() {
    this.tabsMenuData.push(
       {
        tabsName: ["Game Development"],
        projectsContent: UnityProjectsData.dataList
      }
    )
  }
  ngOnDestroy() {
    UnityProjectsData.removeDataList()
  }
}
